@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@200;400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Dancing+Script:wght@700&family=Tangerine&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{margin:0}.navbar{width:calc(100% - 5rem);transition-timing-function:ease-in;transition:all 0.5s;display:flex;justify-content:space-between;align-items:center;padding:1.25rem 2.5rem;background-color:transparent;position:fixed;z-index:5;top:0;font-family:"Raleway", sans-serif}.navbar .navbar-logo{cursor:pointer;font-family:"Tangerine";font-weight:400;font-size:2rem}.navbar .navbar-logo:hover{transform:scale(1.03)}.navbar .navbar-menu{display:flex;align-items:center;cursor:pointer;text-decoration:none}.navbar .navbar-menu a{padding:0.625rem}.navbar .navbar-menu a:hover{transform:scale(1.03)}.navbar .navbar-menu .resume-btn{padding:0.75rem 1rem;margin-left:0.9375rem;border:1px solid #837563;color:#7b8d75;text-decoration:none}.navbar .navbar-menu .resume-btn :hover{background-color:#7b8d75;color:#c0ae98}.navbar-scroll{width:calc(100% - 5rem);z-index:5;transition-timing-function:ease-in;transition:all 0.5s;display:flex;justify-content:space-between;align-items:center;padding:1rem 2.5rem;background-color:#f8faf9;position:fixed;box-shadow:0px 4px 10px rgba(33,68,87,0.3);top:0;font-family:"Raleway", sans-serif}.navbar-scroll .navbar-logo{cursor:pointer;font-family:"Tangerine";font-weight:400;font-size:2rem}.navbar-scroll .navbar-logo:hover{transform:scale(1.03)}.navbar-scroll .navbar-menu{display:flex;align-items:center;cursor:pointer;text-decoration:none}.navbar-scroll .navbar-menu a{padding:0.625rem}.navbar-scroll .navbar-menu a:hover{transform:scale(1.03)}.navbar-scroll .navbar-menu .resume-btn{padding:0.75rem 1rem;margin-left:0.9375rem;border:1px solid #837563}

.sidebar{display:flex;flex-direction:column;align-items:center;position:fixed;left:2.5rem;bottom:0}.sidebar ul{list-style:none;padding-left:0;padding-bottom:1rem}.sidebar a:hover{transform:scale(1.2)}.sidebar .sidebar-line{width:1px;height:6.25rem;border-left:1px solid #7b8d75}.sidebar .sidebar-btn{color:#7b8d75;padding:0.625rem;width:1.45rem;height:1.45rem}.sidebar .sidebar-btn :hover{color:#837563;transition-timing-function:ease-in;transition-duration:150ms}

.sections{padding:0 150px;background-color:#f8faf9}

.header{display:flex;min-height:100vh;justify-content:center;flex-direction:column;padding:0 6.25rem;max-width:1000px}.header p{font-family:"Raleway", sans-serif;line-height:2rem;font-size:1.1rem;color:#7b8d75}.header h1{font-size:5rem;color:#837563}.header h2{font-size:2.5rem;line-height:3.75rem;padding-bottom:1rem;color:#c0ae98}.header a :hover{transition-timing-function:ease-in-out;transition-duration:200ms;cursor:pointer;background-color:#7b8d75;color:#e1d9d6;border:1px solid #7b8d75}.header .contact-btn{font-family:"Open Sans";margin-top:2rem;padding:1.25rem;width:150px;font-size:1.05rem;border:1px solid #837563;color:#837563;background-color:inherit}

.section-title{display:flex;align-items:center}.about{display:flex;flex-direction:column;margin:0 6.25rem;padding:7.75rem 0;position:relative;z-index:3}.section-title{padding-bottom:2rem}.section-title h2{font-size:1.75rem;color:#7b8d75;font-family:"Open Sans"}.tech-list{display:flex;font-family:"Open Sans";color:#7b8d75}.title-line{width:350px;height:1px;border-top:1px solid #7b8d75;margin-left:1rem}.about-container{display:flex;justify-content:space-between}.about-data{font-family:"Raleway";line-height:1.75rem;margin-right:5.25rem}.about-data p{font-size:1rem;padding-bottom:1rem;color:#837563}.about-data p a,.about-data p span{font-family:"Open Sans";font-weight:600;color:#4c5a43}.my-img img{width:300px;height:300px;border-radius:0.25rem}.img-border{width:300px;height:300px;position:absolute;top:14rem;left:670px;border:3px solid #c0ae98;border-radius:0.25rem;z-index:-1}

.experience{margin:0 210px;padding:200px 0 100px}.jobs-table{display:flex}.jobs-table .tabs{display:flex;flex-direction:column;font-family:"Open Sans";font-weight:600;height:100px;cursor:pointer}.jobs-table .tabs .jobs-name{line-height:2.25rem;padding:0.25rem 1rem;height:30px;border-left:3px solid #c0ae98;transition-timing-function:ease-in;transition-duration:250ms;color:#4c5a43}.jobs-table .tabs :hover{background-color:#f0ecea;transition-timing-function:ease-in;transition-duration:250ms}.jobs-table .jobs-active{line-height:2.25rem;padding:0.25rem 1rem;height:30px;border-left:3px solid #7b8d75;transition-timing-function:ease-in;transition-duration:500ms;background-color:#f0ecea;color:#4c5a43}.jobs-table .contents{font-family:"Raleway";width:480px;height:400px;margin-left:2.5rem}.jobs-table .contents h3{line-height:2rem;color:#837563}.jobs-table .contents h3 a{color:#4c5a43;font-family:"Open Sans";font-weight:700}.jobs-table .contents .duration{font-family:"Open Sans";line-height:2rem;font-size:0.9rem;color:#7b8d75;font-weight:600}.jobs-table .contents li{line-height:1.75rem;font-size:1rem;padding-bottom:0.5rem;color:#837563}

.project{margin:0 6.25rem;padding:7rem 0;max-width:1400px}.project1{display:flex;justify-content:space-between;margin-bottom:4rem;position:relative}.project1 .p1-data{display:flex;flex-direction:column;text-align:right}.project1 .p1-data h3{color:#837563;line-height:2rem;font-size:1.45rem}.project1 .p1-data .p1-tools{display:flex;justify-content:flex-end;position:absolute;bottom:40px;right:0}.project1 .p1-data .p1-tools p{padding-left:2rem;font-weight:600;font-family:"Open Sans";color:#837563}.project1 .p1-data .p1-detail{position:absolute;top:60px;right:0;width:480px;height:110px;background-color:#f0ecea;color:#7b8d75;z-index:3;line-height:1.75rem;font-size:1rem;padding:20px 20px 20px 0;border-radius:4px;font-family:"Raleway"}.project1 a :hover{transform:scale(1.03);transition-timing-function:ease-in-out;transition-duration:500ms}.project1 img{width:500px;height:280px;border-radius:4px}.project1 .p1-icons{position:absolute;right:0;bottom:0px}.project2{display:flex;justify-content:space-between;position:relative}.project2 .p2-data{display:flex;flex-direction:column}.project2 .p2-data h3{color:#837563;line-height:2rem;font-size:1.45rem;margin-top:0.5rem}.project2 .p2-data .p2-tools{display:flex;justify-content:flex-end;position:absolute;bottom:55px;left:0}.project2 .p2-data .p2-tools p{padding-right:2rem;font-weight:600;font-family:"Open Sans";color:#837563}.project2 .p2-data .p2-detail{position:absolute;top:105px;left:0;width:480px;height:52px;background-color:#f0ecea;color:#7b8d75;z-index:3;line-height:1.75rem;font-size:1rem;padding:16px 20px 20px 20px;border-radius:4px;font-family:"Raleway"}.project2 .p2-data .p2-icons{position:absolute;left:0;bottom:10px}.project2 a :hover{transform:scale(1.03);transition-timing-function:ease-in-out;transition-duration:500ms}.project2 .img-container{display:flex;justify-content:flex-end}.project2 .img-container img{width:500px;height:280px;border-radius:4px}.project-icons{width:1.5rem;height:1.5rem;color:#7b8d75}.project-icons :hover{color:#837563}.featured{color:#7b8d75;font-family:"Open Sans";font-weight:600}

.contact{display:flex;flex-direction:column;padding:150px 0;text-align:center;max-width:600px;margin:0px auto 90px}.contact h3{line-height:2rem;color:#7b8d75;font-family:"Open Sans"}.contact h1{line-height:5rem;font-size:54px;color:#837563}.contact p{line-height:2rem;font-size:1.1rem;padding-bottom:2rem;font-family:"Raleway";color:#7b8d75}.contact a :hover{cursor:pointer;background-color:#7b8d75;color:#e1d9d6;border:1px solid #7b8d75;transition-timing-function:ease-in-out;transition-duration:200ms}.contact button{font-family:"Open Sans";margin-top:2rem;padding:1.25rem;width:150px;font-size:1.2rem;color:#837563;border:1px solid #837563;background-color:inherit}

.footer{text-align:center;padding-bottom:1.5rem;font-family:"Open Sans";font-weight:600;color:#7b8d75}

.preloader{display:flex;flex-direction:column;justify-content:center;align-items:center;min-height:100vh;background-color:#f8faf9}

